    width: 1220px;
    padding: 0 40px;
  }
}
.button {
  padding: 10px 0;
  text-decoration: none;
  text-transform: uppercase;
  display: inline-block;
  width: 100%;
  color: #ffffff;
  text-align: center;
  font-size: 16px;
  line-height: 20px;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button--down {
  opacity: 0.3;
}
.button:active {
